lex;\n    flex-direction: column;\n    gap: 16px;\n    width: 20%;\n}\n\n#invalid-msg {\n    color: white;\n    font-size: small;\n    border-radius: 10px;\n    text-align: center;\n    padding: 8px;\n}\n\n#matrix-input {\n    display: grid;\n    grid-template-rows: 20% 50% 20%;\n    grid-template-columns: 50% 50%;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n    gap: 8px;\n    height: 45%;\n    padding: 2rem;\n}\n\n#matrix-input p {\n    grid-column: 1 / 4;\n    font-size: x-small;\n    margin: 0;\n}\n\n#matrix-grid {\n    display: grid;\n    grid-template-rows: repeat(3, 35px);\n    grid-template-columns: repeat(3, 30%);\n    gap: 8px;\n    grid-column: 1 / 3;\n    justify-content: center;\n    align-content: center;\n}\n\ninput[type=\"text\"] {\n    color: black !important;\n    font-size: medium;\n    text-align: center;\n    box-sizing: border-box;\n    border-radius: 10px;\n}\n\nbutton {\n    min-width: min-content;\n    height: 40px;\n    border-radius: 10px;\n}\n\n#enter-btn {\n    grid-column: 2 / 3;\n}\n\n#facts {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    text-align: center;\n    height: 55%;\n    overflow: auto;\n    padding: 8px;\n}\n\n#facts .mathjax {\n    max-height: 50%;\n}\n\n#facts p:first-child {\n    margin: -8px;\n}\n\n#viz {\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n    width: 60%;\n}\n\n#options {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n#options div {\n    display: flex;\n    align-items: center;\n    gap: 8px;\n}\n\n#options div:last-child {\n    display: none;\n}\n\n#options div:last-child > div {\n    display: flex;\n    flex-direction: column;\n    align-items: start;\n    gap: 0;\n}\n\n#options div:last-child > div p {\n    margin: 0;\n    font-size: x-small;\n}\n\n#graph {\n    display: grid;\n    flex-grow: 1;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n    justify-items: center;\n    border-radius: 10px;\n    perspective-origin: center;\n    overflow: hidden;\n    position: relative;\n}\n\n#graph * {\n    -webkit-touch-callout: none; \n    -webkit-user-select: none;\n    -khtml-user-select: none; \n    -moz-user-select: none; \n    -ms-user-select: none;\n    user-select: none;\n    zoom: 1;\n}\n\n#cube, #transform-cube, #pyramid, #transform-pyramid {\n    grid-row-start: 1;\n    grid-column-start: 1;\n    width: 70px;\n    height: 70px;\n    transform: rotateX(-25deg) rotateY(-25deg);\n    transform-style: preserve-3d;\n}\n\n#pyramid, #transform-pyramid {\n    transform: rotateX(-25deg) rotateY(-25deg);\n}\n\n#transform-cube {\n    transform: rotateX(-25deg) rotateY(-25deg);\n}\n\n#transform-pyramid {\n    transform: rotateX(-25deg) rotateY(-25deg);\n}\n\n#cube, #square, #pyramid, #triangle { z-index: 1; }\n\n#triangle, .pyramid-side {\n    opacity: 100%;\n    color: transparent;\n}\n\n#transform-cube, #transform-square, #transform-pyramid, #transform-triangle {       \n    z-index: 2; \n}\n\n#cube * { box-sizing: border-box; }\n\n#transform-cube *, #transform-pyramid * { opacity: 60%; }\n\n#cube *, #transform-cube *, #square, #transform-square {\n    position: absolute;\n    width: 70px;\n    height: 70px;\n    color: white;\n}\n\n#triangle, #transform-triangle, .pyramid-side, .transform-pyramid-side {\n    position: absolute;\n    font-size: 70px;\n    text-align: center;\n}\n\n#cube-face-front, #transform-cube-face-front { \n    transform: translateZ(35px); \n}\n\n#cube-face-back, #transform-cube-face-back  { \n    transform: rotateY(180deg) translateZ(35px); \n}\n\n#cube-face-left, #transform-cube-face-left  { \n    transform: rotateY(-90deg) translateZ(35px); \n}\n\n#cube-face-right, #transform-cube-face-right { \n    transform: rotateY(90deg) translateZ(35px);\n}\n\n#cube-face-top, #transform-cube-face-top { \n    transform: rotateX(90deg) translateZ(35px); \n}\n\n#cube-face-bottom, #transform-cube-face-bottom { \n    transform: rotateX(-90deg) translateZ(35px); \n}\n\n#transform-cube-face-front, #transform-square { \n    background-color: #0046ad; \n}\n\n#transform-triangle {\n    color: #0046ad;\n}\n\n#pyramid-side-one, #transform-pyramid-side-one {\n    transform: rotateY(0deg) translateZ(15px) rotateX(35deg);\n}\n\n#pyramid-side-two, #transform-pyramid-side-two {\n    transform: rotateY(90deg) translateZ(15px) rotateX(35deg); \n}\n\n#pyramid-side-three, #transform-pyramid-side-three {\n    transform: rotateY(180deg) translateZ(15px) rotateX(35deg);\n}\n\n#pyramid-side-four, #transform-pyramid-side-four {\n    transform: rotateY(270deg) translateZ(15px) rotateX(35deg);\n}\n  \n#transform-cube-face-back   { background-color: #009b48; }\n\n#transform-cube-face-left   { background-color: #ff5800; }\n\n#transform-cube-face-right  { background-color: #b71234; }\n\n#transform-cube-face-top    { background-color: #ffd500; }\n\n#transform-cube-face-bottom { background-color: #ffffff; }\n\n#transform-pyramid-side-one   { color: #009b48; }\n\n#transform-pyramid-side-two  { color: #ff5800; }\n\n#transform-pyramid-side-three { color: #b71234; }\n\n#transform-pyramid-side-four   { color: #ffd500; }\n\n#transform-square, #transform-triangle {\n    opacity: 60%;\n}\n\n#axes {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    grid-row-start: 1;\n    grid-column-start: 1;\n    transform-style: preserve-3d;\n    transform: rotateX(-25deg) rotateY(-25deg);\n    width: 680px;\n    height: 680px;\n    z-index: 0;\n}\n\n.axis {\n    position: absolute;\n    transform-style: preserve-3d;\n    width: 2px;\n    height: 2px;\n}\n\n.face {\n    position: absolute;\n    width: 680px;  \n    height: 2px;  \n}\n\n#x-axis .face { background-color: red; }\n#y-axis .face { background-color: green; }\n#z-axis .face { background-color: blue; }\n\n\n.face-front { transform: translateZ(1px); }\n.face-back { transform: rotateY(180deg) translateZ(1px); }\n\n.face-left { \n    transform: rotateY(-90deg) translateZ(1px); \n    width: 2px; \n    height: 2px; \n}\n\n.face-right { \n    transform: rotateY(90deg) translateZ(1px); \n    width: 2px; \n    height: 2px; \n}\n\n.face-top { \n    transform: rotateX(90deg) translateZ(1px); \n    width: 680px; \n    height: 2px; \n}\n\n.face-bottom { \n    transform: rotateX(-90deg) translateZ(1px); \n    width: 680px; \n    height: 2px; \n}\n\n#x-axis { transform: translateX(-340px); }\n#y-axis { transform: translateY(-340px) rotateZ(90deg); }\n#z-axis { transform: translateZ(340px) rotateY(90deg); }\n\n#two-d-axes {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    grid-row-start: 1;\n    grid-column-start: 1;\n    transform-style: preserve-3d;\n    z-index: 0;\n}\n\n#two-d-x-axis {\n    position: absolute;\n    width: 5000px;\n    height: 2px;\n    background-color: red;\n}\n\n#two-d-y-axis {\n    position: absolute;\n    width: 5000px;\n    height: 2px;\n    background-color: green;\n    transform: rotateZ(90deg);\n}\n\n#transforms {\n    display: grid;\n    grid-template-rows: 3fr 7fr;\n    grid-template-columns: 1fr 8fr 1fr;\n    row-gap: 16px;\n    flex-grow: 1;\n}\n\n#matrix-mathjax, #transform-stack {\n    border-radius: 10px;\n}\n\n#matrix-mathjax {\n    position: relative;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    grid-column: 1 / 4;\n    font-size: large;\n    overflow: auto;\n}\n\n#matrix-mathjax > div {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: absolute;\n    font-size: large;\n    text-align: center;\n    font-weight: 500;\n    width: 100%;\n    height: 100%;\n    border-radius: 5px;\n}\n\n#matrix-mathjax > div > div {\n    position: relative;\n    padding: 24px;\n}\n\n#transforms-direction, #mmul-direction {\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    font-weight: 500;\n    writing-mode: vertical-rl;\n    text-orientation: upright;\n}\n\n#transforms svg {\n    height: 100px;\n}\n\n#transform-stack {\n    display: flex;\n    flex-direction: column;\n    justify-content: end;\n}\n\n#transform-stack * {\n    font-size: large;\n    height: min-content;\n    width: 100%;\n    border-radius: 5px;\n}\n\n#footer {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-grow: 1;\n}\n\n.paused {\n    -webkit-animation-play-state: paused !important; \n    -moz-animation-play-state: paused !important; \n    -o-animation-play-state: paused !important;\n     animation-play-state: paused !important;\n}\n\n.short-text {\n    display: none;\n}\n\n@keyframes spin { \n    100% { \n        transform:rotate(360deg); \n    } \n}\n\n\n@keyframes swing { \n    0% {\n        transform:rotate(25deg); \n    }\n\n    50% { \n        transform:rotate(-25deg); \n    } \n\n    100% { \n        transform:rotate(25deg); \n    } \n}\n\n@media (max-width: 1200px) {\n    #main {\n        display: grid;\n        grid-template-columns: 70% 30%;\n        overflow: auto;\n    }\n\n    #matrix-facts {\n        flex-direction: row;\n        grid-column: 1 / -1;\n\n        > div {\n            padding: 8px;\n            min-width: 230%;\n            min-height: 100%;\n            box-sizing: border-box;\n        }\n    }\n\n    #viz, #transforms {\n        width: 100%;\n    }\n\n    #transforms-direction, #mmul-direction {\n        display: none;\n    }\n\n    #transform-stack {\n        grid-column: 1 / -1;\n        width: 77%;\n        overflow: auto;\n    }\n\n    #matrix-mathjax {\n        padding: 0;\n        width: 77%;\n        grid-column: 1 / -1;\n        font-size: 2vw;\n    }\n\n    .short-text {\n        display: block;\n    }\n\n    .long-text {\n        display: none;\n    }\n\n    #options {\n        flex-wrap: wrap;\n\n        > div:last-child {\n            flex-wrap: wrap;\n        }\n    }\n\n    #invalid-msg {\n        grid-column: 1 / -1;\n      